import { h } from '@externs/preact'
import { Component } from '@externs/preact'

class Adonais extends Component {
  constructor() {
    super()
    this.state = { hint: null }
    this.setHint = (hint) => {
      this.setState({ hint })
    }
    this.setCoordinates = ({ x, y }) => {
      this.setState({ x, y })
    }
    // this.state = { hint: 'test', x: 10, y:10 }
  }
  render({ splendid }) {
    splendid.export()
    try {
      splendid.addStyle('styles/Adonais.css')
    } catch(err) {/* client */}
    return (h('span',{'className':"Adonais", 'style':"display:inline-block;float:left;position:relative;padding-right:1rem;"},
      h('img',{'src':"img/adonais/back.jpg",'style':"position:relative; opacity:0;"}),
      h('img',{'src':"img/adonais/back.jpg"}),
      this.state.hint && h('span',{'style':{ position: 'absolute', top: this.state.y, left: this.state.x },'className':"AdonaisCopy"},
        h('span',{'className':"AdonaisNoise"},this.state.hint),
      ),
      h(Letter,{top:112,file:"img/adonais/a2.png",left:219,hint:this.state.hint,class:"A",setHint:this.setHint,setCoordinates:this.setCoordinates},
        h('strong',{'className':"Letter"},`A`),`rt Deco creates products for the modern web using back-end Node.JS And front-end JavaScript, taking advantages of the best available methods.`
      ),
    )
    )
  }
}

const Letter = ({ file, top, left, setHint, children, setCoordinates, class: className, hint }) => {
  return (h('img',{'className':className, 'src':file, 'style':{ 'top': `${top}px`, 'left': `${left}px` }, 'onMouseOver':() => {
    setHint(children)
  }, 'onMouseOut':() => {
    setHint(null)
    // this.setState({ hint: null })
  }, 'onMouseMove':(e) => {
    if (!hint) return
    const img = /** @type {HTMLImageElement} */ (e.currentTarget)
    const state = {
      x: parseInt(img.style.left, 10) + e.offsetX,
      y: parseInt(img.style.top, 10) + e.offsetY,
    }
    setCoordinates(state)
    // this.setState(state)
  }}))
}

export default Adonais